.Bullet_Points {
    display: flex;
    margin: 5px 0px;
    align-items: center;
    font-weight: bold; /* Add font weight */
}
container {
  background-image:  linear-gradient(90deg, rgba(255,255,255,0) 0%, XXXXXXXX 100%);
            height: auto;
            background-attachment: fixed;
            background-size: cover;
}
.Bullet_Points p {
    margin: 0px 5px;
    font-weight: bold; /* Add font weight */
}

.templatesList {
    font-family: "Poppins";
    color: #fff;
    text-align: center;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    font-weight: bold; /* Add font weight */
}

.template {
    font-family: "Poppins";
    color: #fff;
    text-align: center;
    height: 300px;
    width: 200px;
    overflow: hidden;
    border-radius: 13px;
    margin: auto;
    border: 1px solid black;
    margin: 10px;
    font-weight: bold; /* Add font weight */
}

.template img {
    max-width: 100%;
    min-height: 100%;
    max-height: 100%;
}

.Animate
{
   text-transform: uppercase;
  background-image: linear-gradient(
    -225deg,
    #6283d7 0%,
    #784da3 29%,
    rgb(32, 32, 218) 67%,
    #f8f0ed 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 10s ease-in-out infinite;
  display: inline-block;
      font-size: 50px;
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}
